@font-face {
	font-family  : 'TTHoves';
	src          : url('../fonts/TTHoves-Light.woff2') format('woff2'),
	url('../fonts/TTHoves-Light.woff') format('woff'),
	url('../fonts/TTHoves-Light.ttf') format('truetype');
	font-weight  : 300;
	font-style   : normal;
	font-display : swap;
}

